import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index'
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
    if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
    return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        props: true,
        component: Home
    },
    {
        path: "/search",
        name: "Search",
        component: () =>
            import ('@/views/Search')
    },
    {
        path: "/orderdetails",
        name: "Orderdetails",
        props:true,
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Orderdetails')
    },
    // Delivergoods
    {
        path: "/delivergoods",
        name: "Delivergoods",
        props:true,
        component: () =>
            import ('@/views/Delivergoods')
    },
    {
        path: "/category",
        name: "Category",

        component: () =>
            import ('@/views/Category')
    },
    {
        path: "/payment",
        name: "Payment",

        component: () =>
            import ('@/views/Payment')
    },
    {
        path: "/paymentsuccessful",
        name: "Paymentsuccessful",
        props:true,
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Paymentsuccessful')
    },
    {
        path: "/paymentlist",
        name: "Paymentlist",
        props:true,
        component: () =>
            import ('@/views/Paymentlist')
    },
    {
        path: "/cart",
        name: "Cart",
      
       
        meta: {isLogin:true  }, //表示进入该组件需要i登录才能进入
        component: () =>
            import ('@/views/Cart')
    },
    {
        path: "/my",
        name: "My",
        meta: { isLogin: true },
        component: () =>
            import ('@/views/My')
    },
    {
        path: "/login",
        name: "Login",
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Login')
    },

    {
        path: "/goodList/:id",
        name: "GoodList",
        props: true,

        component: () =>
            import ('@/views/GoodList')
    }, {

        path: "/detail/:id",
        name: "Detail",
        props: true,
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Detail')
    },
    {

        path: "/salesdetails",
        name: "Salesdetails",
       
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Salesdetails')
    },

    {

        path: "/dzi",
        name: "Dzi",
       
        meta: { hidenNav: true },
        component: () =>
            import ('@/views/Dzi')
    },

   
 
    {
        path: "*",
        name: "NotFound",
        component: () =>
            import ('@/views/NotFound')

    }

]

const router = new VueRouter({
    mode: 'hash',
    routes
});
router.beforeEach((to, from, next) => {
    if (to.meta.isLogin) { //判断需要登录才能继续操作.
        if (!store.getters.token) { //判断没有登录. 去到登录.
            next('/login')
        }
    }
    next();
});

export default router