import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)
    //vuex 管理vue全局共享状态. 
    //保证状态以一种可预测的方式发生变化.(多个地方修改获取 保证有序状态.)
export default new Vuex.Store({
    state: {
        token: null,
        userInfo: null
    },
    getters: {
        token(state) {
            //获取 vuex里面token如果这个为空 就去 localStorage取
            let t = state.token;
            if (!t) {
                t = localStorage.getItem('token');
                
            }
            
            return t;
        },
        userInfo(state) { //获取登录用户信息
            //获取 vuex里面token如果这个为空 就去 localStorage取
            let t = state.userInfo;
            if (!t) {
                t = JSON.parse(localStorage.getItem('userInfo'));
            }
            return t;
        }
    },
    mutations: {
        login(state, payload) { //登录存储token数据
            state.token = payload;
            //把登录token存入 localStorage
            localStorage.setItem('token', payload);
            document.cookie = "token=" + payload
            
        },
        
        setUser(state, payload) {
            state.userInfo = payload;
        },
        addcart(state,payload){
            state.cartNU=payload;
            console.log(state.cartNU);
          },
          addshou(state,payload){
            state.shoumi=payload;
            console.log(state.shoumi);
            // let t=JSON.parse(localStorage.getItem('shoumi'));
            // console.log(t)
            // localStorage.setItem('shoumi',JSON.stringify(state.shoumi));
          }
        
    },
    actions: {
        getLoginUser(contenxt, payload) {
            //userinfo
            axios({
                method: "get",
                url: "http://47.115.51.185/api/userinfo",
                headers: {
                    'Authori-zation': 'Bearer ' + payload
                }
            }).then(d => {
                contenxt.commit('setUser', d.data.data);
                localStorage.setItem('userInfo', JSON.stringify(d.data.data));
                console.log('获取信息==>', d);
            })
        }
    },
    modules: {}
})